}

#typing{
	width: 37ch;
	animation: typing 4s steps(22), blink .5s step-end infinite alternate;
	white-space: nowrap;
	overflow: hidden;
	border-right: 3px solid;
}

@keyframes typing {
	from{
		width: 0;
	}	
}

@keyframes blink {
	50%{
		border-color: transparent;
	}
}

